.no-margin {
  margin: 0;
}

.no-padding {
  padding: 0;
}

.texto_centrado {
  text-align: center;
}

.texto_justificado {
  text-align: justify;
}

.texto_derecha {
  text-align: right;
}

.texto_izquierda {
  text-align: left;
}

.mayuscula {
  text-transform: capitalize;
}

.all-mayuscula {
  text-transform: uppercase;
}

.no-visible {
  display: none;
}

/** Media Queries **/
body {
  /* background-color: white; */
}

#app {
  display: flex;
  flex-direction: column;
  grid-gap: 1rem;
  margin: auto;
}

.trabajo {
  display: flex;
  flex-direction: column;
}

h1, h2 {
  /* color: green; */
}

button {
  padding: 1rem 1rem;
}

ul li {
  color: black;
}

ul {
  list-style-type: decimal;
}

.fruta {
  margin-bottom: 2rem;
}

.Mouse {
  display: flex;
  flex-direction: column;
  grid-gap: 1rem;
  margin-top: 2rem;
}
.Mouse .subtotal {
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.Mouse .Calcu {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1rem;
}
.Mouse h3 {
  display: flex;
  justify-content: center;
  color: brown;
}
.Mouse .reset {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 1rem;
}

.Post {
  margin-top: 1rem;
  display: flex;
  flex-direction: column;
  grid-gap: 1rem;
}
.Post .botones {
  display: flex;
  flex-direction: row;
  grid-gap: 0.7rem;
}
.Post .boton-Post {
  margin: auto;
  width: 100%;
}
.Post .boton-blo {
  background-color: rgb(229, 229, 229) !important;
  pointer-events: none;
}

.caja-1 {
  margin: auto;
}
.caja-1 .caja-body {
  display: flex;
  flex-direction: column;
  border: 2px solid red;
  padding: 1rem 1.5rem;
  border-radius: 1rem;
}
.caja-1 .card-title {
  margin: 0 auto 1rem;
  font-family: serif;
  font-size: 1rem;
  color: brown;
}
.caja-1 p {
  font-family: "Times New Roman", Verdana, serif;
  font-size: 0.7rem;
  margin: 0 auto;
}
.caja-1 button {
  margin: 1rem auto 0.5rem;
  border-radius: 10rem;
  padding: 0.8rem;
  font-size: 0.7rem;
  background-color: white;
  font-weight: bold;
}
.caja-1 button:hover {
  background-color: #37C0FF;
  color: white;
}

.prueba {
  padding: 0.5rem;
}
.prueba .lista {
  display: flex;
  flex-direction: row;
  grid-gap: 0.8rem;
  margin: auto;
  height: 70%;
  transform: scale(1);
  width: 90%;
  overflow-x: scroll;
  margin-bottom: 1rem;
  /* position: fixed; */
}

nav a {
  color: #6E75FF;
}

.Register, .Login, .Home, .Editar {
  display: flex;
  flex-direction: column;
}
.Register h1, .Login h1, .Home h1, .Editar h1 {
  margin: 2.5rem auto;
}
.Register .input-register, .Login .input-register, .Home .input-register, .Editar .input-register {
  background-color: #505050;
  padding: 0.4rem;
  width: 12rem;
  margin: auto;
}
.Register button, .Login button, .Home button, .Editar button {
  margin: 1rem auto;
  padding: 1rem 1.5rem;
}
.Register button:hover, .Login button:hover, .Home button:hover, .Editar button:hover {
  background-color: white;
  color: #242424;
}
.Register .Userli, .Login .Userli, .Home .Userli, .Editar .Userli {
  padding: 0;
}
.Register .Userli li, .Login .Userli li, .Home .Userli li, .Editar .Userli li {
  display: flex;
  flex-direction: column;
  margin: 0.5rem auto;
  min-width: 12rem;
  border: white 2px solid;
  border-radius: 0.7rem;
  padding: 0.5rem;
}
.Register .Userli li span, .Login .Userli li span, .Home .Userli li span, .Editar .Userli li span {
  color: white;
  margin: 0.2rem auto;
  font-size: 0.8, rem;
  display: inline-block;
  max-width: 15rem; /* Ajusta este valor según sea necesario */
  overflow: hidden;
  text-overflow: ellipsis; /* Muestra puntos suspensivos (...) si el texto se recorta */
  white-space: nowrap;
}
.Register .Userli li .li-Botones, .Login .Userli li .li-Botones, .Home .Userli li .li-Botones, .Editar .Userli li .li-Botones {
  display: flex;
  flex-direction: row;
}
.Register .Userli li .li-Botones button, .Login .Userli li .li-Botones button, .Home .Userli li .li-Botones button, .Editar .Userli li .li-Botones button {
  padding: 1rem auto;
}

.inputs {
  display: flex;
  flex-direction: column;
  grid-gap: 1rem;
}